import React, { ReactNode, useState } from 'react';
import classNames from 'classnames';

import Button, { ButtonProps } from '../Button/Button';
import { Container } from '../Container';
import { SvgClose } from '../Icons';
import { Stack } from '../Stack';
import { Text } from '../Typography';

import styles from './Notification.module.scss';

export interface NotificationProps {
    type?: 'full-width' | 'inline';
    severity?: 'notify' | 'alert' | 'warning' | 'success';
    icon?: ReactNode;
    title?: string;
    description?: string;
    close?: boolean;
    closeButtonAriaLabel?: string;
    closeButtonTooltipLabel?: ButtonProps['tooltipLabel'];
    children?: ReactNode;
}

export const Notification = ({
    type = 'full-width',
    severity = 'notify',
    icon,
    title,
    description,
    close,
    closeButtonAriaLabel,
    closeButtonTooltipLabel,
    children,
}: NotificationProps) => {
    const [isVisible, setIsVisible] = useState(true);

    const handleClose = () => {
        setIsVisible(false);
    };

    if (!isVisible) return null;

    const iconWithClassname = React.cloneElement(icon as React.ReactElement, {
        className: styles.icon,
    });

    const content = (
        <div className={styles.inner}>
            <Stack direction={{ xsmall: 'row' }}>
                {icon && <>{iconWithClassname}</>}
                <div
                    className={styles.content}
                    aria-live="assertive"
                    aria-atomic="true"
                >
                    {title && (
                        <Text
                            className={styles.title}
                            weight="600"
                            variant="line"
                        >
                            {title}
                        </Text>
                    )}

                    {description && (
                        <Text className={styles.description} variant="p">
                            {description}
                        </Text>
                    )}
                    {children && (
                        <div className={styles.children}>{children}</div>
                    )}
                </div>
            </Stack>
            {close && (
                <Button
                    variant="tertiary"
                    onPress={handleClose}
                    isIcon
                    aria-label={closeButtonAriaLabel}
                    tooltipLabel={closeButtonTooltipLabel}
                    tooltipWrapperClassName={styles['inner__close-button']}
                    className={styles['inner__close-button']}
                >
                    {/* TODO: Discuss with designers for the button being everywhere the same */}
                    <SvgClose aria-hidden="true" />
                </Button>
            )}
        </div>
    );

    return (
        <div
            className={classNames(styles.base, {
                [styles['base--inline']]: type === 'inline',
                [styles['base--alert']]: severity === 'alert',
                [styles['base--warning']]: severity === 'warning',
                [styles['base--success']]: severity === 'success',
            })}
        >
            {type === 'full-width' ? <Container>{content}</Container> : content}
        </div>
    );
};
