import React, { forwardRef, ReactNode, useId, useRef } from 'react';
import {
    AriaCheckboxProps,
    useCheckbox,
    useFocusRing,
    VisuallyHidden,
} from 'react-aria';
import { useToggleState } from 'react-stately';
import { mergeProps, useObjectRef } from '@react-aria/utils';
import classNames from 'classnames';

import { Stack } from '../Stack';
import { Label, Text } from '../Typography';

import styles from './ChoiceToggle.module.scss';

export interface ChoiceToggleProps extends AriaCheckboxProps {
    children?: ReactNode;
    label: string;
    description?: string;
    layout?: 'row' | 'column';
}

const ChoiceToggle = forwardRef<HTMLInputElement, ChoiceToggleProps>(
    (props, ref) => {
        const forwardedRef = useObjectRef(ref);
        const fallbackRef = useRef(null);
        const currentRef = forwardedRef || fallbackRef;
        const { children, label, description, layout = 'row' } = props;

        const state = useToggleState(props);
        const { isSelected } = state;
        const { inputProps, isPressed, isInvalid, labelProps } = useCheckbox(
            props,
            state,
            currentRef
        );

        const { isFocusVisible, focusProps } = useFocusRing();

        const descriptionId = useId();

        const choiceClassList = classNames(styles.choice, {
            [styles[`choice--pressed`]]: isPressed,
            [styles[`choice--selected`]]: isSelected,
            [styles[`choice--focused`]]: isFocusVisible,
            [styles[`choice--invalid`]]: isInvalid,
        });

        const layoutClassList = classNames({
            [styles['column']]: layout === 'column',
            [styles['row']]: layout === 'row',
        });

        const { 'aria-describedby': ariaDescribedBy, ...restInputProps } =
            inputProps;

        return (
            <div className={classNames(choiceClassList, layoutClassList)}>
                <Label className={styles.label} {...labelProps}>
                    <span
                        className={styles.choice__indicator}
                        aria-hidden="true"
                    >
                        {/* TODO: replace with icon from library when it is added */}
                        <svg
                            width="16"
                            height="16"
                            viewBox="0 0 16 16"
                            xmlns="http://www.w3.org/2000/svg"
                            className={styles.choice__icon}
                        >
                            <circle cx="8" cy="8" r="5" />
                        </svg>
                    </span>

                    <VisuallyHidden>
                        <input
                            {...mergeProps(restInputProps, focusProps)}
                            ref={currentRef}
                            aria-describedby={`${descriptionId} ${ariaDescribedBy}`}
                        />
                    </VisuallyHidden>

                    <div className={styles.choice__content}>
                        <Stack>
                            <div className={styles.choice__title}>{label}</div>
                            {description && (
                                <Text
                                    id={descriptionId}
                                    variant="line"
                                    size="small"
                                    weight="400"
                                    className={styles.choice__description}
                                >
                                    {description}
                                </Text>
                            )}
                        </Stack>
                    </div>
                </Label>
                {children && (
                    <div className={styles['custom-content']}>{children}</div>
                )}
            </div>
        );
    }
);

ChoiceToggle.displayName = 'ChoiceToggle';

export default ChoiceToggle;
