import React from 'react';
import { FocusRing } from 'react-aria';
import classNames from 'classnames';

import { useTabsContext } from '../Tabs/Tabs';
import { Label, Text } from '../Typography';

import styles from './Tab.module.scss';

export interface TabProps {
    id: string;
    label: string;
    description?: string;
}

const Tab = ({ label, description, id }: TabProps) => {
    const { activeTab, setActiveTab } = useTabsContext();

    const isActive = activeTab === id;

    const handleClick = () => {
        setActiveTab(id);
    };

    const classList = classNames(styles.tab, {
        [styles['tab--active']]: isActive,
        [styles['tab--has-description']]: description,
    });

    return (
        <FocusRing focusRingClass={styles['tab--focus']}>
            <button
                id={id}
                role="tab"
                aria-controls={`${id}-panel`}
                aria-selected={isActive}
                className={classList}
                onClick={handleClick}
            >
                <Label className={styles.tab__title} weight="600">
                    {label}
                </Label>
                {description && (
                    <Text
                        variant="line"
                        weight="400"
                        className={styles.tab__description}
                    >
                        {description}
                    </Text>
                )}
            </button>
        </FocusRing>
    );
};

export default Tab;
