import React, { forwardRef, useRef } from 'react';
import {
    AriaLinkOptions,
    mergeProps,
    useFocusRing,
    useLink,
    useObjectRef,
} from 'react-aria';
import classNames from 'classnames';

import { SvgArrowRight } from '../Icons';
import { Heading, Text } from '../Typography';

import styles from './QuickLink.module.scss';

export interface QuickLinkProps extends AriaLinkOptions {
    title: string;
    description: string;
    href?: string;
    target?: string;
    className?: string;
}

export const QuickLink = forwardRef<HTMLAnchorElement, QuickLinkProps>(
    (props, ref) => {
        const forwardedRef = useObjectRef(ref);
        const fallbackRef = useRef(null);
        const linkRef = forwardedRef || fallbackRef;
        const { title, description, href, target, className } = props;
        const { linkProps } = useLink(props, linkRef);
        const { focusProps, isFocusVisible } = useFocusRing();

        return (
            <a
                className={classNames(styles.base, className, {
                    [styles[`focus-ring`]]: isFocusVisible,
                })}
                {...mergeProps(linkProps, focusProps)}
                ref={linkRef}
                href={href}
                target={target}
            >
                <div className={styles.header}>
                    <Heading variant="h5">{title}</Heading>
                    <div className={styles.icon}>
                        <SvgArrowRight aria-hidden="true" />
                    </div>
                </div>
                <div className={styles.description}>
                    <Text>{description}</Text>
                </div>
            </a>
        );
    }
);

QuickLink.displayName = 'QuickLink';
